import React,{Component} from 'react'
import { hashHistory } from 'react-router';
import { Layout, Breadcrumb, Button, Table, Popconfirm, message } from 'antd';
const { Content } = Layout;

export class List extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            columns: [{
                title: '活动标题',
                dataIndex: 'title',
                className: "column-width-8"
            }, {
                title: '活动人数',
                dataIndex: "limitNum",
                className: "column-width-5"
            }, {
                title: '报名人数',
                className: "column-width-5",
                render: data => <div className="divClick" onClick={this.goToSignUpList.bind(this, data.key)}>{data.signUpNum}</div>
            }, {
                title: '活动状态',
                dataIndex: "status",
                className: "column-width-7",
                render: status => status == "1" ? "报名中" : "结束"
            }, {
                title: '活动开始时间',
                className: "column-width-12",
                dataIndex: 'startTime'
            }, {
                title: '活动结束时间',
                className: "column-width-12",
                dataIndex: 'endTime'
            }, {
                title: '报名截止时间',
                className: "column-width-12",
                dataIndex: 'deadlineTime'
            }, {
                title: '活动添加时间',
                className: "column-width-12",
                dataIndex: 'createTime'
            }, {
                title: '状态',
                className: "column-width-5",
                dataIndex: 'active',
                render: active => active == "0" ? "无效" : "有效"
            }, {
                title: "操作",
                className: "column-width-22",
                dataIndex: "key",
                render: key => <div style={{"textAlign": "center"}}>
                    <Button type="primary" onClick={this.detailInfo.bind(this, key)} style={{"marginRight": "10px"}}>查看
                    </Button>
                    <Button onClick={this.editInfo.bind(this, key)} style={{"marginRight": "10px"}}>修改
                    </Button>
                    <Popconfirm placement="left" title="是否确认删除?" onConfirm={this.deleteInfo.bind(this, key)} okText="确认" cancelText="取消">
                        <Button type="danger">删除</Button>
                    </Popconfirm></div>
            }],
            dataSource: [],
            pagination: {
                current: 1,
                total: 0
            },
            loading: false
        };
        ['changePage'].forEach(m=>this[m] = this[m].bind(this))
    }

    componentWillMount() {
        this.getInfoList(1);
    }

    getInfoList(currentPage) {
        let that = this;
        this.setState({loading: true});
        ajax({
            url: "/activity/getInfoList",
            data: {
                    current: currentPage ? currentPage : 1
            },
            callback: function(data) {
                that.setState(
                    {
                        loading: false,
                        pagination: {
                            current: currentPage,
                            total: data.data.total
                        },
                        dataSource: data.data.list
                    }
                )
            }
        });
    }

    addActivity() {
        hashHistory.push("/activity/add")
    }

    detailInfo(id) {
        hashHistory.push("/activity/detail/" + id);
    }

    editInfo(id) {
        hashHistory.push("/activity/edit/" + id)
    }

    goToSignUpList(id) {
        hashHistory.push("/activity/signUp/" + id);
    }

    deleteInfo(id) {
        let that = this;
        ajax({
            url: "/activity/deleteInfo",
            data: {id: id},
            callback: function(data) {
                if(data.status == 200) {
                    message.success("删除成功", 1);
                    let newDataSource = that.state.dataSource.filter(item => { return item.key != id});
                    that.setState({dataSource: newDataSource});
                } else {
                    message.error(data.errMsg, 1);
                }
            }
        });
    }

    changePage(pagination) {
        this.getInfoList(pagination.current)
    }

    render() {
        return (
            <Layout style={{ padding: '0 24px 24px' }}>
                <Breadcrumb style={{ margin: '12px 0' }}>
                    {
                        this.props.routes.map(function(item, index) {
                            return <Breadcrumb.Item key="">{item.breadcrumbName}</Breadcrumb.Item>
                        })
                    }
                </Breadcrumb>
                <Content style={{ background: '#fff', padding: 24, margin: 0, minHeight: 280 }}>
                    <Button icon="plus" onClick={this.addActivity} style={{"marginBottom": "20px"}}>添加活动</Button>
                    <Table
                        bordered
                        dataSource={this.state.dataSource}
                        columns={this.state.columns}
                        pagination={this.state.pagination}
                        onChange={this.changePage}
                        loading={this.state.loading}
                    />
                </Content>
            </Layout>
        )
    }
}

module.exports = List;


